* {
  font-family: Inter !important;
}

.container-body {
  height: 100vh;
  margin-left: 130px;
  margin-right: 130px;
  margin-top: 15px;
}

.box-data-account {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #3e1051;
  border-radius: 10px;
  padding: 16px;
  position: relative;
  overflow: hidden;
}

.image-profile-container {
  width: 12.5rem;
  height: 12.5rem;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f0f0;
}

.image-profile {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.icon-edit {
  color: #c0c2d0 !important;
}

.icon-edit:hover {
  color: #ed8131 !important;
  border: 1px solid #ed8131 !important;
  border-radius: 5px;
}

.name-account {
  font-size: 24px !important;
  font-weight: 700 !important;
  color: #fff !important;
  line-height: 44.6px !important;
}

.description-account {
  font-size: 15px !important;
  font-weight: 300 !important;
  color: #fff !important;
  line-height: 21.6px !important;
}

.box-verifications-account {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  border-radius: 10px;
  padding: 16px;
  position: relative;
  border: 1px solid #dbdce5;
  margin-top: 16px;
}

.box-verifications-account-links {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  padding: 16px;
  position: relative;
  margin-top: 10px;
  margin-bottom: 2rem;
}

.verificaciones {
  font-size: 15px !important;
  font-weight: 700 !important;
  color: #3e1051 !important;
  line-height: 40.6px !important;
}

.verification-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 8px;
}

.verification-item .MuiTypography-root {
  font-size: 15px !important;
  font-weight: 300 !important;
  color: #747684;
  line-height: 21.6px !important;
}

.box-verifications-account-links .MuiTypography-root {
  font-size: 15px !important;
  font-weight: 300 !important;
  color: #747684;
  line-height: 21.6px !important;
  text-align: center;
  margin: 0.1rem;
}

.seccion2 {
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 40px;
}

.menu-seccion2 {
  border-bottom: 1px solid #dbdce5;
}

.buttons-seccion2 {
  font-size: 18px !important;
  font-weight: 700 !important;
  border-radius: 0 !important;
  text-transform: none !important;
  margin-right: 18px !important;
}

.buttons-seccion2:hover {
  background-color: transparent !important;
  color: #3e1051;
}

.image-list {
  width: 100%;
  display: grid;
  grid-auto-rows: 134px;
  overflow: visible;
  gap: 17px !important;
}

.box-image {
  position: relative;
  height: 134px;
  width: 100%;
  overflow: hidden;
  margin-bottom: 17px !important;
  border-radius: 10px !important;
}

.box-button-image {
  position: absolute !important;
  top: 90px !important;
  right: 5px !important;
  background-color: #ffffff !important;
  width: 40px !important;
  height: 40px !important;
  border-radius: 6px !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  padding: 0px !important;
}

.box-document {
  margin-top: 20px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.text-doc {
  font-size: 16px !important;
  font-weight: 300 !important;
  color: #747684;
  line-height: 21.6px !important;
  margin-right: 20px !important;
}

.info-title {
  font-size: 16px !important;
  font-weight: 700 !important;
  color: #3e1051;
  line-height: 21.6px !important;
}

.info-subtitle {
  font-size: 16px !important;
  font-weight: 300 !important;
  color: #747684;
  line-height: 21.6px !important;
  margin-bottom: 24px !important;
  margin-right: 50px !important;
}

.info-rrss {
  font-size: 16px !important;
  font-weight: 300 !important;
  color: #747684;
  line-height: 21.6px !important;
}

.box-seccion3 {
  border: 1px solid #dbdce5;
  border-radius: 10px;
  height: auto;
  padding: 20px;
  margin-bottom: 40px;
}

.box-publicidad {
  height: auto;
}

.box-publicidad img {
  width: 100%;
  margin-bottom: 1rem;
}

.box-publicidad h2 {
  /* Titulo */

  width: auto;
  height: auto;

  /* 700/16px */
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;

  color: #000000;

  /* Inside auto layout */
  flex: none;
  order: 1;
  align-self: stretch;
  flex-grow: 0;
  margin-bottom: 0.5rem;
}

.box-publicidad p {
  /* Lorem ipsum dolor sit amet consectetur. Venenatis elementum a quisque porttitor. */

width: auto;
height: auto;

/* 400/14px */
font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 17px;

/* Grey/200 */
color: #747684;


/* Inside auto layout */
flex: none;
order: 2;
align-self: stretch;
flex-grow: 0;

}

.title-seccion3 {
  font-size: 14px !important;
  font-weight: 700 !important;
  color: #e3602d;
  line-height: 21.6px !important;
}

.price-seccion3 {
  font-size: 28px !important;
  font-weight: 700 !important;
  color: #191a24;
  line-height: 40.6px !important;
  margin-bottom: 28px !important;
}

.service-seccion3 {
  font-size: 18px !important;
  font-weight: 700 !important;
  color: #191a24;
  line-height: 40.6px !important;
}

.user-data {
  background-color: #ffffff;
  width: 100%;
  border-radius: 10px;
  margin-top: 15px !important;
}

.buttons-user-data {
  display: flex;
  justify-content: row;
  align-items: center;
  align-self: flex-end;
  width: 70%;
  gap: 10px;
  margin-bottom: 7px !important;
  overflow: hidden;
}

.button-user-data {
  flex-grow: 1;
  border-radius: 12px !important;
  text-transform: none !important;
  font-weight: 400 !important;
  font-size: 13px !important;
  line-height: 15.6px !important;
  padding: 5px !important;
}

.text-error {
  font-family: Libre Baskerville !important;
  font-size: 28px !important;
  font-weight: 700 !important;
  line-height: 34.72px !important;
  color: #3e1051 !important;
}

@media (max-width: 600px) {
  .seccion2 {
    margin-top: 40px;
    margin: 0px !important;
  }

  .menu-seccion2 {
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #747684;
    margin-top: 40px;
  }

  .buttons-seccion2 {
    font-size: 16px !important;
    font-weight: 700 !important;
    line-height: 16.6px !important;
    border-radius: 0 !important;
  }

  .container-body {
    height: 100vh;
    margin-left: 30px;
    margin-right: 30px;
    margin-top: 15px;
  }

  .box-seccion3 {
    margin-top: 40px;
  }
}

@media (min-width: 900px) and (max-width: 1500px) {
  .buttons-user-data {
    width: 95%;
    margin-bottom: 0.5rem !important;
    gap: 0.0005rem !important;
  }

  .button-user-data {
    font-size: 12px !important;
    line-height: 10.6px !important;
    margin-right: 0.5rem !important;
    margin-left: 0.5rem !important;
  }
}

@media (min-width: 1600px) {
  .image-profile-container {
    width: 15.5rem;
    height: 15.5rem;
  }

  .buttons-user-data {
    width: 65%;
  }
}
